import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
        {/* Resume Section
   ================================================== */}
<section id="resume">
  {/* Education
----------------------------------------------- */}
  <div className="row education">
    <div className="three columns header-col">
      <h1><span>Education</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h4>South-Eastern Finland University of Applied Sciences</h4>
          <p className="info">B.A. Degree in Game Programming <span>•</span> <em className="date">August 2019 - Present</em></p>
          <p>     
          We get to know about common game engines and our head programming languages are C++ and C#. We learn about game business and monetization of games. 
          The studies are focused on gameproject work. In our studies we process how to design, test and implement a game.

          </p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Education */}
  {/* Work
----------------------------------------------- */}
  <div className="row work">
    <div className="three columns header-col">
      <h1><span>Internship</span></h1>
    </div>
    <div className="nine columns main-col">
      <div className="row item">
        <div className="twelve columns">
          <h4>Dakar</h4>
          <p className="info">Programmer <span>•</span> <em className="date">June 2020 - Present</em></p>
          <p>
          I work in the company as a programmer.
          We are implementing a web page for the company using the React and Laravel framework tool. 
          I designed a site recommendation algorithm and I was involved in building the page.
          </p>
        </div>
      </div> {/* item end */}
      <div className="row item">
        <div className="twelve columns">
          <h4>Innocode</h4>
          <p className="info">Software Developer <span>•</span> <em className="date">June 2019 - July 2019</em></p>
          <p>
        I worked in the company as a software developer.
        I was able to design and make a demo with a Unity game engine of a 3D spectrogram.
          </p>
        </div>
      </div> {/* item end */}
    </div> {/* main-col end */}
  </div> {/* End Work */}
</section> {/* Resume Section End*/}

      </React.Fragment>
    );
  }
}