import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
      {/* Header
   ================================================== */}
<header id="home">
  <nav id="nav-wrap">
    <a className="mobile-btn" href="#nav-wrap" title="Show navigation">Show navigation</a>
    <a className="mobile-btn" href="/#" title="Hide navigation">Hide navigation</a>
    <ul id="nav" className="nav">
      <li className="current"><a className="smoothscroll" href="#home">Home</a></li>
      <li><a className="smoothscroll" href="#portfolio">Projects</a></li>
      <li><a className="smoothscroll" href="#resume">Resume</a></li>
    </ul> {/* end #nav */}
  </nav> {/* end #nav-wrap */}
  <div className="row banner">
    <div className="banner-text">
      <h1 className="responsive-headline">I'm Tarita Karhulahti.</h1>
      <h3>I'm from Finland and I'm studying for a degree in <span>Bachelor of Engineering </span> specialization in <span>game programming</span>. Games are my <span>passion</span> and I enjoy working with games. Let's start watching my<a className="smoothscroll" href="#portfolio"> projects </a>
        and learn more <a className="smoothscroll" href="#resume">about me</a>.</h3>
      <hr />
      <ul className="social">
        <li><a href="https://www.linkedin.com/in/tarita-karhulahti-b97819200/"><i className="fa fa-linkedin" /></a></li>
        <li><a href="https://github.com/TaraTorpeedo"><i className="fa fa-github" /></a></li>
      </ul>
    </div>
  </div>
  <p className="scrolldown">
    <a className="smoothscroll" href="#portfolio"><i className="icon-down-circle" /></a>
  </p>
</header> {/* Header End */}
      </React.Fragment>
    );
  }
}